:local .panel {
  padding: 25px 30px;
  margin-bottom: 10px;
  position: relative;
  border-radius: 2px;
  background-color: #fff;
  border: solid 1px #e5eaea;
  display: flex; }
  @media (max-width: 640px) {
    :local .panel {
      display: block;
      padding: 1rem; } }

:local .image {
  width: 120px;
  height: 120px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  margin-right: 30px; }
  @media (max-width: 640px) {
    :local .image {
      width: 100%;
      margin-bottom: 10px; } }

:local .content {
  min-height: 120px;
  flex: 1; }
  @media (max-width: 640px) {
    :local .content {
      min-height: 0; } }

:local .title {
  font-size: 20px;
  font-weight: bold;
  line-height: 1.3;
  color: #333;
  margin-bottom: 10px; }

:local .detail {
  font-size: 12px;
  line-height: 1.67;
  color: #666;
  margin-top: 5px;
  margin-bottom: 20px;
  display: flex;
  flex: 1; }
  @media (max-width: 640px) {
    :local .detail {
      display: none; } }

:local .link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

:local .tag {
  position: relative;
  z-index: 1;
  flex: 1; }
  :local .tag a {
    font-size: 12px;
    color: #7c9aa4;
    margin: 0 5px;
    display: inline-block; }
    :local .tag a:hover {
      text-decoration: underline !important; }

:local .stat {
  text-align: right;
  font-size: 13px;
  line-height: 1.38;
  color: #333; }
